<template>
  <el-container class="home-container">
    <!-- 头部 -->
    <el-header>
      <div class="logo-navbar">
        <img src="../assets/logo.png" alt />
        <span>电商管理后台</span>
      </div>
      <el-button @click="logout">注销</el-button>
    </el-header>
    <el-container>
      <!-- 左侧菜单栏 -->
      <el-aside :width="collapse?'64px':'200px'">
        <!-- 折叠菜单栏按钮 -->
        <div class="collapse-btn" @click="toggleCollapse">
          <i :class="collapse?'el-icon-s-unfold':'el-icon-s-fold'"></i>
        </div>
        <el-menu
          :collapse="collapse"
          :default-active="defaultActive"
          class="el-menu-vertical-demo"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#409eff"
          unique-opened
          router
          :collapse-transition="false"
        >
          <!-- 一级目录 -->
          <el-submenu :index="item.id+''" v-for="item in menuList" :key="item.id">
            <template slot="title">
              <!-- 图标 -->
              <i :class="iconList[item.id]"></i>
              <!-- 菜单栏文本 -->
              <span>{{item.authName}}</span>
            </template>
            <!-- 二级目录 index 1 代表不高亮 2 代表高亮-->
            <el-menu-item
              :index="'/'+subItem.path"
              v-for="subItem in item.children"
              :key="subItem.id"
              @click="saveActiveState('/'+subItem.path)"
            >
              <i class="el-icon-menu"></i>
              <span slot="title">{{subItem.authName}}</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <!-- 路由占位符 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      menuList: [],
      // 服务端未给出菜单栏图标，自定义
      iconList: {
        '125': 'el-icon-user-solid',
        '103': 'el-icon-s-management',
        '101': 'el-icon-s-goods',
        '102': 'el-icon-s-order',
        '145': 'el-icon-s-marketing'
      },
      // 是否折叠菜单栏
      collapse: false,
      // 默认高亮显示的菜单栏
      defaultActive: ''
    }
  },
  created() {
    this.getList()
    this.defaultActive = window.sessionStorage.getItem('default_active')
  },
  methods: {
    logout() {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    async getList() {
      const { data, meta } = (await this.$http.get('menus')).data
      if (meta.status === 200) {
        this.menuList = data
      } else {
        this.$message.error('获取权限列表失败')
      }
    },
    toggleCollapse() {
      this.collapse = !this.collapse
    },
    // 由于每次切换都会重置home，无法在单页面存储点击栏目的状态，将菜单栏高亮的状态存储在sessionStorage中
    saveActiveState(path) {
      window.sessionStorage.setItem('default_active', path)
      this.defaultActive = window.sessionStorage.getItem('default_active')
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}
// 头部区域
.el-header {
  background-color: #003366;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 25px;
  > div {
    display: flex;
    align-items: center;
    > span {
      margin-left: 15px;
    }
  }
}

.logo-navbar img {
  width: 60px;
  height: 60px;
  padding: 10px;
  box-sizing: border-box;
}
.el-main {
  background-color: rgb(232, 232, 241);
}
.el-aside {
  background-color: #545c64;
  color: white;
  .el-menu {
    border-right: none;
  }
  .collapse-btn {
    font-size: 30px;
    text-align: right;
    padding-right: 10px;
    background-color: #4a5064;
    cursor: pointer;
  }
}
</style>
