<template>
  <div class="login-container">
    <div class="login-box"  v-loading.fullscreen.lock="fullscreenLoading">
      <div class="avator-box">
        <img src="../assets/logo.png" alt />
      </div>
      <!-- 登录表单区域 -->
      <el-form
        label-width="0px"
        class="login-form"
        :model="form"
        :rules="LoginFormRules"
        ref="loginFromRef"
      >
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input placeholder="用户名" prefix-icon="el-icon-s-custom" v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            placeholder="密码"
            prefix-icon="el-icon-lock"
            v-model="form.password"
            type="password"
            v-on:keyup.enter.native="login"
          ></el-input>
        </el-form-item>
        <el-form-item class="btn-wrapper">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button type="info" @click="resetLoginFrom">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 是否显示加载进度
      fullscreenLoading: false,
      form: {
        username: '',
        password: ''
      },
      // 表单校验规则
      LoginFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '长度在 6 到 16 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 点击重置按钮，重置表单
    resetLoginFrom() {
      this.$refs.loginFromRef.resetFields()
    },
    login() {
      this.$refs.loginFromRef.validate(async valid => {
        if (valid) {
          this.fullscreenLoading = true

          const { data, meta } = (await this.$http.post(
            'login',
            this.form
          )).data

          this.fullscreenLoading = false
          if (meta.status === 200) {
            this.$message.success('登录成功')
            window.sessionStorage.setItem('access_token', data.token)
            this.$router.push('/home')
          } else {
            this.$message.error(meta.msg)
          }
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login-container {
  background: #006633;
  width: 100%;
  height: 100%;
}
.login-box {
  width: 450px;
  height: 300px;
  background: white;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}
.avator-box {
  height: 130px;
  width: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  box-shadow: 2px 5px 5px #eee;
  padding: 10px;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  background: white;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: #eee;
  }
}
// 表单
.login-form {
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 0 25px;
  box-sizing: border-box;
}
.btn-wrapper {
  display: flex;
  justify-content: flex-end;
}
</style>
