<template>
  <div>
    <!-- 表头 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主体内容 -->
    <el-card class="box-card">
      <!-- 添加角色按钮 -->
      <el-row>
        <el-col>
          <el-button
            type="primary"
            style="margin-bottom:20px"
          >添加角色</el-button>
        </el-col>
      </el-row>
      <!-- 角色列表表格 -->
      <el-table
        :data="mRolesList"
        style="width: 100%"
        border
        v-loading="loading"
        row-key="id"
      >
        <!-- 展开列表权限 children里面是当前角色的三级权限 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <div>
              <el-row
                v-for="(item,i) in scope.row.children"
                :key="item.id"
                :class="[i===0?'borderd-top':'','borderd-bottom','dispaly-center ']"
              >
                <el-col :span="5">
                  <!-- 根据角色和角色iD删除权限 必须传入角色的原因是需要做不刷新页面的缓存 -->
                  <el-tag
                    closable
                    @close="removeTagById(scope.row,item.id)"
                  >{{item.authName}}</el-tag>
                  <i class="el-icon-arrow-right"></i>
                </el-col>
                <el-col :span="19">
                  <el-row
                    v-for="(subItem,j) in item.children"
                    :key="subItem.id"
                    :class="[j!==0?'borderd-top':'','dispaly-center']"
                  >
                    <el-col :span="5">
                      <el-tag
                        type="danger"
                        closable
                        @close="removeTagById(scope.row,subItem.id)"
                      >{{subItem.authName}}</el-tag>
                      <i class="el-icon-arrow-right"></i>
                    </el-col>
                    <el-col :span="14">
                      <el-row
                        v-for="(grandItem,k) in subItem.children"
                        :key="grandItem.id"
                        :class="[k!==0?'borderd-top':'','dispaly-center']"
                      >
                        <el-tag
                          closable
                          @close="removeTagById(scope.row,grandItem.id)"
                          type="warning"
                        >{{grandItem.authName}}</el-tag>
                      </el-row>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </div>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column
          label="角色名称"
          prop="roleName"
        ></el-table-column>
        <el-table-column
          label="角色描述"
          prop="roleDesc"
        ></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              class="el-icon-edit"
            >编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              class="el-icon-delete"
            >删除</el-button>
            <el-button
              size="mini"
              type="warning"
              class="el-icon-s-tools"
              @click="showPermissionDialog(scope.row)"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 树形控件弹窗 -->
    <el-dialog
      title="分配权限"
      :visible.sync="permissionDialogVisible"
      width="30%"
      @close="resetDefaultKeys"
    >
      <el-tree
        :data="mPermissionList"
        :props="treeProps"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-expanded-keys="defaultKeys"
        :default-checked-keys="defaultKeys"
        ref="treeRef"
      ></el-tree>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="permissionDialogVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="allotsPermissions"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data() {
    return {
      // 是否先是加载
      loading: false,
      // 角色列表 children里面是三级权限
      mRolesList: [],
      // 权限列表
      mPermissionList: [],
      // 显示树控件的模板
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // 树形控件默认选中的权限ID
      defaultKeys: [],
      // 分配权限弹窗-树形控件 是否可见
      permissionDialogVisible: false,
      // 选中元素当前的角色ID
      mRoleId: 0
    }
  },
  methods: {
    //  得到权限列表
    async fetchRolesList() {
      this.loading = true
      const { meta, data } = (await this.$http.get('roles')).data
      if (meta.status === 200) {
        var cloneData = _.cloneDeep(data)
        // 要做一个遍历，删除子节点
        // cloneData = this.deleteLeafNode(cloneData)
        this.mRolesList = cloneData
        console.log(this.mRolesList)
      } else {
        this.$message.error(meta.msg)
      }
      this.loading = false
    },
    deleteLeafNode(data) {
      console.log(data)
      for (let i = 0; i < data.length; i++) {
        data[i].children = null
      }
      return data
    },
    async fetchPermissionList() {
      this.loading = true
      const { meta, data } = (await this.$http.get('rights/tree')).data
      if (meta.status === 200) {
        this.mPermissionList = data
        // console.log(data)
      } else {
        this.$message.error(meta.msg)
      }
      this.loading = false
    },
    // 显示权限分配对话框，并且下拉数据
    showPermissionDialog(role) {
      this.mRoleId = role.id
      this.fetchPermissionList()
      this.getIdByLeafNode(role, this.defaultKeys)
      this.permissionDialogVisible = true
    },
    // 遍历权限的树形结构，获得默认权限ID
    getIdByLeafNode(node, arr) {
      if (!node.children) return arr.push(node.id)
      node.children.forEach(item => {
        this.getIdByLeafNode(item, arr)
      })
    },
    // 点击确定，分配权限
    async allotsPermissions() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      var rids = keys.join(',')
      const { meta } = (await this.$http.post(`roles/${this.mRoleId}/rights`, {
        rids
      })).data
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.$message.success('更新成功！')
      // 有点乱这里，因为设置role去渲染权限列表 权限列表是所有权限,不是当前角色的权限,所以是刷新当前角色拥有的权限列表
      // 权限总列表是不会随便改变的
      // this.fetchPermissionList()
      this.fetchRolesList()
      this.permissionDialogVisible = false
    },
    // 关闭对话框，需要重置默认选中权限列表
    resetDefaultKeys() {
      this.defaultKeys = []
    },
    // 通过角色id和权限id删除权限
    // roles/:roleId/rights/:rightId
    removeTagById(role, permissionId) {
      this.$confirm('此操作将永久删除该权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { meta, data } = (await this.$http.delete(
            `roles/${role.id}/rights/${permissionId}`
          )).data
          if (meta.status === 200) {
            this.$message.success('删除成功!')
            // 再次调用获取权限列表，增加服务器负担，只需要在页面做缓存，将返回的数据赋值给缓存的角色列表
            // 也不会关闭权限列表
            role.children = data
          }
        })
        .catch(() => {
          this.$message.info('已取消删除')
        })
    }
  },
  created() {
    this.fetchRolesList()
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 10px;
}
.borderd-top {
  border-top: 1px solid #f1f1f1;
}
.borderd-bottom {
  border-bottom: 1px solid #f1f1f1;
}
// 纵向居中对齐
.dispaly-center {
  display: flex;
  align-items: center;
}
</style>
