<template>
  <div>
    <!-- 表头 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- 级联选择器规则的提示栏 -->
      <el-alert title="注意：只允许第三级分类设置相关参数！" type="warning" show-icon></el-alert>
      <!-- 级联选择器 -->
      <el-row class="cascader">
        <el-col>
          <span>选择商品分类：</span>
          <el-cascader
            v-model="selectedOption"
            :options="mCateList"
            :props="cascaderProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- TAB栏 静态属性 动态参数 标签页-->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- 添加静态属性面板 -->
        <el-tab-pane label="动态参数" name="many">
          <el-button
            type="primary"
            :disabled="isBtnDisable"
            size="mini"
            @click="addDialogVisible = true"
          >添加参数</el-button>
          <!-- 动态参数的表格-->
          <el-table :data="dynamicData" style="width: 100%" border stripe v-loading="loading">
            <!-- 标签展开行 -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <div>
                  <!-- 显示的标签组 -->
                  <el-tag
                    v-for="(item,i) in scope.row.attr_vals"
                    :key="i"
                    closable
                    @close="handleClose(i,scope.row)"
                  >{{item}}</el-tag>
                  <!-- 添加新标签的输入框 -->
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  ></el-input>
                  <!-- 添加新标签的按钮 点击转为输入框 -->
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scope.row)"
                  >添加新标签</el-button>
                </div>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="参数名称" prop="attr_name"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  class="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                >编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  class="el-icon-delete"
                  @click="remove(scope.row.attr_id)"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!-- 添加动态参数面板 -->
        <el-tab-pane label="静态属性" name="only">
          <el-button
            type="primary"
            :disabled="isBtnDisable"
            size="mini"
            @click="addDialogVisible = true"
          >添加属性</el-button>
          <!-- 静态属性表格 -->
          <el-table :data="staticData" style="width: 100%" border stripe v-loading="loading">
            <el-table-column type="expand">
              <!-- 静态属性 - 标签展开行 -->
              <template slot-scope="scope">
                <div>
                  <!-- 显示的标签组 -->
                  <el-tag
                    v-for="(item,i) in scope.row.attr_vals"
                    :key="i"
                    closable
                    @close="handleClose(i,scope.row)"
                  >{{item}}</el-tag>
                  <!-- 添加新标签的输入框 -->
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  ></el-input>
                  <!-- 添加新标签的按钮 点击转为输入框 -->
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scope.row)"
                  >添加新标签</el-button>
                </div>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="属性名称" prop="attr_name"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  class="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                >编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  class="el-icon-delete"
                  @click="remove(scope.row.attr_id)"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- 添加参数的对话框 -->
    <el-dialog
      :title="'添加'+titleText"
      :visible.sync="addDialogVisible"
      width="30%"
      @close="resetForm"
    >
      <el-form :model="form" :rules="rules" ref="formRef">
        <el-form-item prop="attr_name" :label="titleText">
          <el-input placeholder="参数名称" v-model="form.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addParam">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改参数的对话框 -->
    <el-dialog
      :title="'编辑'+titleText"
      :visible.sync="editDialogVisible"
      width="30%"
      @close="resetEditForm"
    >
      <el-form :model="editForm" :rules="rules" ref="editFormRef">
        <el-form-item prop="attr_name" :label="titleText">
          <el-input placeholder="参数名称" v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editParam">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data() {
    return {
      // 局部表单显示加载进度
      loading: false,
      // 分类列表
      mCateList: [],
      //   配置级联选择器属性
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      //   选中的属性
      selectedOption: [],
      //   标签页默认选中的页 many是动态参数 only是静态属性
      activeName: 'many',
      //   动态参数面板的数据展示
      dynamicData: [],
      // 静态属性面板的数据展示
      staticData: [],
      //   是否先是添加参数或属性的对话框
      addDialogVisible: false,
      // 添加属性参数表单验证规则
      rules: {
        attr_name: [{ required: true, message: '请输入名称', trigger: 'blur' }]
      },
      //   添加表单数据序列化
      form: {
        attr_name: '',
        attr_sel: '',
        attr_vals: ''
      },
      //   修改表单数据序列化
      editForm: {
        attr_id: 0,
        attr_name: '',
        attr_sel: '',
        attr_vals: ''
      },
      // 是否显示修改对话框
      editDialogVisible: false
    }
  },
  methods: {
    async fetchCategoriesList() {
      const { meta, data } = (await this.$http.get('categories')).data
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mCateList = data
      // console.log(this.mCateList)
    },
    // 处理标签页被点击后
    handleClick(tab, event) {
      this.getParams()
    },
    // 处理级联选择器被选中后
    handleChange() {
      this.getParams()
    },
    // 发送请求，根据面页，获取参数列表
    async getParams() {
      if (this.isBtnDisable) {
        // 将静态属性和动态参数的列表做清空
        this.dynamicData = []
        this.staticData = []
        this.selectedOption = []
        return
      }
      /**
      // 如果有缓存数据，就不需要请求服务器
      if (this.dynamicData.length !== 0 && this.staticData.length !== 0) {
        return
      }**/
      if (this.cateID) {
        this.loading = true

        const { meta, data } = (await this.$http.get(
          `categories/${this.cateID}/attributes`,
          {
            params: {
              sel: this.activeName
            }
          }
        )).data

        this.loading = false
        if (meta.status !== 200) return this.$message.error(meta.msg)
        data.forEach(item => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          // 为每一类分类参数 分配 新添加的输入框控制
          item.inputVisible = false
          item.inputValue = ''
        })
        // console.log(data)
        if (this.activeName === 'many') {
          this.dynamicData = data
        } else if (this.activeName === 'only') {
          this.staticData = data
        }
      }
    },
    // 清空添加参数或属性表单
    resetForm() {
      this.$refs.formRef.resetFields()
    },
    // 清空修改参数或属性表单
    resetEditForm() {
      this.$refs.editFormRef.resetFields()
    },
    // 发送添加参数请求 首先要判断表单是否通过验证
    addParam() {
      this.$refs.formRef.validate(async valid => {
        if (!valid) return

        this.form.attr_sel = this.activeName
        const { meta, data } = (await this.$http.post(
          `categories/${this.cateID}/attributes`,
          this.form
        )).data
        console.log(data)

        if (meta.status !== 201) return this.$message.error(meta.msg)
        this.$message.success(`添加${this.titleText}成功`)
        this.addDialogVisible = false
        /**
        *在本地做了数据缓存，就不能暴力从远端刷新，应该是添加到本地数据缓存
        data.attr_vals = data.attr_vals ? data.attr_vals.split(' ') : []
        // 为每一类分类参数 分配 新添加的输入框控制
        if (this.activeName === 'only') {
          this.staticData.push(data)
          console.log(this.staticData)
        } else {
          this.dynamicData.push(data)
          console.log(this.dynamicData)
        }
        */
        this.getParams()
      })
    },
    // 先是需要查询数据库里最新的参数名称，显示出来
    async showEditDialog(attrId) {
      this.editDialogVisible = true
      const { meta, data } = (await this.$http.get(
        `categories/${this.cateID}/attributes/${attrId}`
      )).data

      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.editForm.attr_name = data.attr_name
      this.editForm.attr_id = data.attr_id
    },
    // 发送修改对话框的请求
    editParam() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { meta } = (await this.$http.put(
          `categories/${this.cateID}/attributes/${this.editForm.attr_id}`,
          {
            attr_sel: this.activeName,
            attr_name: this.editForm.attr_name
          }
        )).data
        if (meta.status !== 200) this.$message.error(meta.msg)
        this.$message.success(`修改${this.titleText}成功`)
        this.editDialogVisible = false
        this.getParams()
      })
    },
    // 删除某个参数的请求
    async remove(attrId) {
      const { meta } = (await this.$http.delete(
        `categories/${this.cateID}/attributes/${attrId}`
      )).data
      if (meta.status !== 200) this.$message.error(meta.msg)
      this.$message.success(`删除${this.titleText}成功`)
      this.getParams()
    },
    // 添加新标签，显示文本输入框
    showInput(attrItem) {
      attrItem.inputVisible = true
      // 输入框自动获得焦点
      // nextTick 函数是等待DOM元素被渲染之后，才开始调用回调函数
      // 设置输入框可见后，不一定将输入框渲染出来，此时让输入框获得焦点，输入框是空的
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 添加新标签，文本框失去焦点，或按下enter键触发
    // 将attrItem作为参数传递，attrItem本身指向又是缓存数据，所以需要一份克隆去传递参数，成功之后，再去缓存中添加或删除
    async handleInputConfirm(attrItem) {
      if (attrItem.inputValue.trim().length !== 0) {
        // 如果刷新失败的话
        const copyAttrItem = _.cloneDeep(attrItem)
        copyAttrItem.attr_vals.push(attrItem.inputValue)
        const res = this.updateAttrVals(copyAttrItem, attrItem)
        console.log(res)
        res.then(resolve => {
          console.log(resolve)
          if (resolve) attrItem.attr_vals.push(attrItem.inputValue)
          // 当文本框失去焦点或者按下enter键后，输入框变为按钮
          attrItem.inputVisible = false
          attrItem.inputValue = ''
        })
      }
    },
    // 删除标签
    async handleClose(i, attrItem) {
      const copyAttrItem = _.cloneDeep(attrItem)
      copyAttrItem.attr_vals.splice(i, 1)
      const res = this.updateAttrVals(attrItem)
      res.then(resolve => {
        if (resolve) attrItem.attr_vals.splice(i, 1)
      })
    },
    // 将更改的标签数组推送到远端
    async updateAttrVals(copyAttrItem, attrItem) {
      // 发送请求刷新到远端,远端刷新成功，才能添加到缓存数据中显示
      const { meta } = (await this.$http.put(
        `categories/${this.cateID}/attributes/${copyAttrItem.attr_id}`,
        {
          attr_name: copyAttrItem.attr_name,
          attr_sel: copyAttrItem.attr_sel,
          attr_vals: copyAttrItem.attr_vals.join(' ')
        }
      )).data
      if (meta.status !== 200) {
        this.$message.error(meta.msg)
        return false
      }
      this.$message.success('更新标签成功')
      return true
    }
  },
  created() {
    this.fetchCategoriesList()
  },
  computed: {
    //   如果按钮被禁用,返回true, 不需要返回false
    isBtnDisable() {
      if (this.selectedOption.length !== 3) {
        return true
      }
      return false
    },
    // 被级联选择器选中之后获得的ID
    cateID() {
      if (this.selectedOption.length === 3) {
        return this.selectedOption[2]
      }
      return null
    },
    // 添加属性和参数的弹框-标题
    titleText() {
      if (this.activeName === 'only') return '静态属性'
      return '动态参数'
    }
  }
}
</script>

<style scoped>
.cascader {
  margin-top: 15px;
}
/* 新签组的样式 */
.el-tag {
  margin: 5px;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>
