<template>
  <div>
    <!-- 表头 git提交 文件改名不敏感大小写-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- 添加分类按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary" style="margin-bottom:20px" @click="showAddDialog">添加分类</el-button>
        </el-col>
      </el-row>
      <!-- 商品分类表格 -->
      <tree-table
        :data="mCategoriesList"
        style="width: 100%"
        :columns="columns"
        :show-index="true"
        border
        :expand-type="false"
        :show-row-hover="false"
        :selection-type="false"
        v-loading="loading"
      >
        <!-- 是否有效列 -->
        <template slot="catDeletedTmpl" slot-scope="scope">
          <div>
            <i
              class="el-icon-success"
              v-if="scope.row.cat_deleted===false"
              style="color:lightgreen;"
            ></i>
            <i class="el-icon-error" v-else style="color:red;"></i>
          </div>
        </template>
        <!-- 排序列 -->
        <template slot="orderTmpl" slot-scope="scope">
          <div>
            <el-tag v-if="scope.row.cat_level === 0">一级</el-tag>
            <el-tag type="success" v-else-if="scope.row.cat_level === 1">二级</el-tag>
            <el-tag type="warning" v-else>三级</el-tag>
          </div>
        </template>
        <!-- 操作栏目 -->
        <template slot="optTmpl">
          <div>
            <el-button type="primary" size="mini" icon="el-icon-edit">编辑</el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete">删除</el-button>
          </div>
        </template>
      </tree-table>
      <!-- 分页栏目 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[3, 4, 5, 7]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="mTotal"
      ></el-pagination>
    </el-card>
    <!-- 添加分类对话框 -->
    <el-dialog title="添加分类" :visible.sync="addCateVisible" @close="resetForm">
      <el-form :model="cateForm" ref="cateFormRef" :rules="rules">
        <el-form-item label="分类名称：" prop="cat_name">
          <el-input v-model="cateForm.cat_name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="父级分类：">
          <el-cascader
            v-model="selectedOption"
            :options="mParentsList"
            :props="cascadeProps"
            @change="handleChange"
            class="cascader"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addCateVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCategory">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 是否加载按钮
      loading: false,
      queryInfo: {
        type: '',
        pagenum: 1,
        pagesize: 7
      },
      //   商品分类列表
      mCategoriesList: [],
      //   商品分类总数
      mTotal: 0,
      // 每一行的数据定义
      columns: [
        { label: '分类名称', prop: 'cat_name' },
        {
          label: '是否有效',
          type: 'template',
          template: 'catDeletedTmpl'
        },
        {
          label: '排序',
          type: 'template',
          template: 'orderTmpl'
        },
        {
          label: '操作',
          type: 'template',
          template: 'optTmpl'
        }
      ],
      // 是否先是添加分类对话框
      addCateVisible: false,
      // 添加分类的表单序列化
      cateForm: {
        // 分类名称
        cat_name: '',
        // 如果要添加1级分类，则父分类Id应该设置为  0
        cat_pid: 0,
        // 0表示一级分类；1表示二级分类；2表示三级分类
        cat_level: 0
      },
      // 分类名称输入框规则
      rules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' },
          { min: 2, max: 6, message: '长度在 2 到 6 个字符', trigger: 'blur' }
        ]
      },
      // 级联选择器的配置
      cascadeProps: {
        // 指定选项的值为选项对象的某个属性值
        value: 'cat_id',
        // 指定选项标签为选项对象的某个属性值
        label: 'cat_name',
        // 指定选项的子选项为选项对象的某个属性值
        children: 'children',
        // 次级菜单的展开方式 鼠标滑动展示
        expandTrigger: 'hover',
        // 选中任一级选项
        checkStrictly: true
      },
      // 父级分类列表
      mParentsList: [],
      // 被选中的级联选择
      selectedOption: []
    }
  },
  methods: {
    async fetchProductList() {
      this.loading = true

      const { meta, data } = (await this.$http.get('categories', {
        params: this.queryInfo
      })).data

      this.loading = false
      if (meta.status === 200) {
        this.mCategoriesList = data.result
        this.mTotal = data.total
      } else {
        this.$message.error(meta.msg)
      }
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.fetchProductList()
    },
    handleCurrentChange(curSize) {
      this.queryInfo.pagenum = curSize
      this.fetchProductList()
    },
    // 得到父级的分类列表
    async fetchParentsList() {
      const { meta, data } = (await this.$http.get('categories', {
        params: { type: 2 }
      })).data
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mParentsList = data
      console.log(this.mParentsList)
    },
    // 展示添加分类的对话框
    showAddDialog() {
      this.fetchParentsList()
      this.addCateVisible = true
    },
    // 当选中节点变化时触发
    // cat_pid 如果要添加1级分类，则父分类Id应该设置为  0
    // cat_level 0表示添加一级分类；1表示添加二级分类；2添加表示三级分类
    handleChange() {
      // 如果被选中的选项有值，就是添加二级分类和三级分类
      // 如果没有被选中，说明添加顶级分类
      if (this.selectedOption.length > 0) {
        const index = this.selectedOption.length - 1
        this.cateForm.cat_pid = this.selectedOption[index]
        this.cateForm.cat_level = this.selectedOption.length
      } else {
        this.cateForm.cat_pid = 0
        this.cateForm.cat_level = 0
      }
    },
    // 发送请求，添加分类
    addCategory() {
      this.$refs.cateFormRef.validate(async valid => {
        if (!valid) return
        const { meta } = (await this.$http.post(
          'categories',
          this.cateForm
        )).data
        console.log(meta)

        if (meta.status !== 201) return this.$message.error(meta.msg)
        this.$message.success('添加分类成功')
        this.fetchProductList()
        this.addCateVisible = false
      })
    },
    // 对话框关闭，重置表单
    resetForm() {
      this.$refs.cateFormRef.resetFields()
      this.cateForm.cat_pid = 0
      this.cateForm.cat_level = 0
      this.selectedOption = []
    }
  },
  created() {
    this.fetchProductList()
  }
}
</script>

<style scoped>
.cascader {
  width: 100%;
}
</style>
