<template>
  <div>
    <!-- 表头 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- 查询订单输入框 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            class="input-with-select"
            clearable
          >
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!-- 展示订单列表表格 -->
      <el-table
        ref="orderTable"
        :data="mOrderList"
        style="width: 100%"
        border
        stripe
        v-loading="loading"
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column
          prop="order_number"
          label="订单编号"
        ></el-table-column>
        <el-table-column
          prop="order_price"
          label="订单价格（元）"
          width="95px"
        ></el-table-column>
        <el-table-column
          prop="pay_status"
          label="是否付款"
          width="80px"
        >
          <!-- 订单状态： 0 未付款、1 已付款 -->
          <template slot-scope="scope">
            <div>
              <el-tag
                type="success"
                v-if="scope.row.pay_status === '1'"
              >已付款</el-tag>
              <el-tag
                type="error"
                v-else
              >未付款</el-tag>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="is_send"
          label="是否发货"
          width="80px"
        ></el-table-column>
        <el-table-column
          prop="create_time"
          label="下单时间"
          width="170px"
        >
          <template slot-scope="scope">
            <div>{{scope.row.create_time|dateFormat}}</div>
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          width="130px"
        >
          <template slot-scope="scope">
            <!-- 编辑按钮 -->
            <el-button
              size="mini"
              type="primary"
              class="el-icon-map-location"
              @click="addressVisible=true"
            ></el-button>
            <!-- 删除按钮 -->
            <el-button
              size="mini"
              type="success"
              class="el-icon-truck"
              @click="queryExpressInfoById(scope.row.order_number)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页栏目 加上background 属性，可以获得 页面背景的颜色-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 25, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="mTotal"
        background
      ></el-pagination>
    </el-card>
    <!-- 修改地址对话框 -->
    <el-dialog
      title="修改地址"
      :visible.sync="addressVisible"
      width="50%"
      @close="resetForm"
    >
      <el-form
        :model="addressForm"
        ref="addressFormRef"
        :rules="addressFormRules"
      >
        <el-form-item
          label="省市区/县"
          prop="address"
        >
          <el-cascader
            v-model="addressForm.address1"
            :options="cityData"
            :props="{ expandTrigger: 'hover' }"
          ></el-cascader>
        </el-form-item>
        <el-form-item
          label="详细地址"
          prop="detailAddr"
        >
          <el-input
            v-model="addressForm.address2"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="addressVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="editAddress"
        >确 定</el-button>
      </span>
    </el-dialog>
    <!-- 展示物流信息对话框 -->
    <el-dialog
      title="物流信息"
      :visible.sync="expressVisible"
      width="50%"
    >
      <el-steps
        direction="vertical"
        :active="1"
      >
        <el-step
          :title="item.ftime"
          v-for="(item,i) in expressList"
          :key="i"
          :description="item.context"
        ></el-step>
      </el-steps>
    </el-dialog>
  </div>
</template>

<script>
import cityData from '../../plugins/citydata.js'
export default {
  data() {
    return {
      // 局部表单显示加载进度
      loading: false,
      // 查询订单需要的请求信息
      queryInfo: {
        pagenum: 1,
        pagesize: 10
      },
      // 订单列表数据
      mOrderList: [],
      //   订单数据的条目总数
      mTotal: 0,
      // 修改地址对话框
      addressForm: {},
      // 是否显示对话框
      addressVisible: false,
      // 存储城市数据的本地
      cityData,
      // 修改地址表单验证规则
      addressFormRules: {
        address: [
          { required: true, message: '请选择省市区/县', trigger: 'blur' }
        ],
        detailAddr: [
          { required: true, message: '请填写详细地址', trigger: 'blur' }
        ]
      },
      // 物流快递信息
      expressList: [],
      // 是否显示物流信息对话框
      expressVisible: false
    }
  },
  methods: {
    async fetchOrderList() {
      this.loading = true
      const { meta, data } = (await this.$http.get('orders', {
        params: this.queryInfo
      })).data
      this.loading = false
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mOrderList = data.goods
      this.mTotal = data.total
      console.log(data)
    },
    // 查询物流信息
    async queryExpressInfoById(id) {
      console.log(id)

      this.expressVisible = true
      const { data } = (await this.$http.get('/kuaidi/' + '1106975712662')).data
      // if (meta.status !== 200) this.$message.error(meta.msg)
      console.log(data)
      this.expressList = data
    },
    // 分页按钮处理分页的最大条目
    handleSizeChange(size) {
      this.queryInfo.pagesize = size
      this.fetchOrderList()
    },
    // 选择当前页码
    handleCurrentChange(curPage) {
      this.queryInfo.pagenum = curPage
      this.fetchOrderList()
    },
    // 填写对话框表单，发送修改表单请求
    editAddress() {
      this.$refs.addressFormRef.validate(async valid => {
        if (!valid) return this.$message.error('请正确填写')
        this.$message.succcess('没有写这块的接口')
      })
    },
    // 关闭对话框后重置表单
    resetForm() {
      this.$refs.addressFormRef.resetFields()
    }
  },
  created() {
    this.fetchOrderList()
  }
}
</script>

<style scoped>
.el-cascader {
  width: 100%;
}
</style>
