import Vue from 'vue'
import Router from 'vue-router'
import Login from './components/Login.vue'
import Home from './components/Home.vue'
import Welcome from './components/Welcome.vue'
import Users from './components/Users.vue'
import Rights from './components/permission/Rights.vue'
import Roles from './components/permission/Roles.vue'
import Categories from './components/product/Categories.vue'
import Params from './components/product/Params.vue'
import GoodsList from './components/product/List.vue'
import AddProduct from './components/product/AddProduct.vue'
import Reports from './components/reports/Reports.vue'
import Order from './components/order/Order.vue'

Vue.use(Router)
const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/home',
      component: Home,
      redirect: '/welcome',
      children: [
        {
          path: '/welcome',
          component: Welcome
        },
        {
          path: '/users',
          component: Users
        },
        {
          path: '/rights',
          component: Rights
        },
        {
          path: '/roles',
          component: Roles
        },
        {
          path: '/categories',
          component: Categories
        },
        {
          path: '/params',
          component: Params
        },
        {
          path: '/goods',
          component: GoodsList
        },
        {
          path: '/goods/add',
          component: AddProduct
        },
        {
          path: '/reports',
          component: Reports
        }, {
          path: '/orders',
          component: Order
        }
      ]
    }
  ]
})
router.beforeEach((to, from, next) => {
  if (to.path === '/login') return next()
  const token = window.sessionStorage.getItem('access_token')
  if (token) return next()
  return next('/login')
})
export default router
