<template>
  <div>
    <!-- 表头 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主体内容 -->
    <el-card class="box-card">
      <!-- 权限列表表格 -->
      <el-table ref="pTable" :data="mPermissionList" style="width: 100%" border  v-loading="loading">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="authName" label="权限名称" column-key="id"></el-table-column>
        <el-table-column prop="path" label="路径"></el-table-column>
        <el-table-column prop="level" label="权限等级">
          <!-- 通过插槽，将权限等级转化为图标 -->
          <template slot-scope="scope">
            <el-tag
              :type="levelClass[scope.row.level]"
              disable-transitions
            >{{levelClassText[scope.row.level]}}</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 是否显示加载
      loading: false,
      mPermissionList: [],
      type: 'list',
      levelClass: ['primary', 'success', 'warning'],
      levelClassText: ['一级', '二级', '三级']
    }
  },
  methods: {
    async getPermissionList() {
      // 显示加载对话框
      this.loading = true
      // 发送请求
      const { meta, data } = (await this.$http.get('rights/' + this.type)).data
      if (meta.status === 200) {
        // console.log(data)
        this.mPermissionList = data
      } else {
        this.$message.error(meta.msg)
      }
      // 隐藏加载
      this.loading = false
    }
  },
  created() {
    this.getPermissionList()
  }
}
</script>

<style lang="less" scoped>
</style>
