<template>
  <div>
    <!-- 表头 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="10">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            class="input-with-select"
            v-model="queryInfo.query"
            clearable
            v-on:keyup.enter.native="getUsersList"
          >
            <el-button slot="append" icon="el-icon-search" @click="getUsersList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addFormVisible = true">添加用户</el-button>
        </el-col>
      </el-row>
      <!-- 用户信息列表 -->
      <el-table :data="mUserList" style="width: 100%" v-loading="loading" border stripe>
        <el-table-column label="姓名" prop="username"></el-table-column>
        <el-table-column label="邮箱" prop="email"></el-table-column>
        <el-table-column label="电话" prop="mobile"></el-table-column>
        <el-table-column label="角色" prop="role_name"></el-table-column>
        <el-table-column label="状态" prop="mg_state">
          <template slot-scope="socpe">
            <div>
              <el-switch v-model="socpe.row.mg_state" @change="changeUserState(socpe.row)"></el-switch>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180px">
          <template slot-scope="socpe">
            <!-- 编辑按钮 -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(socpe.row.id)"
            ></el-button>
            <!-- 删除按钮 -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUser(socpe.row.id)"
            ></el-button>
            <!-- 分配角色按钮 -->
            <el-tooltip class="item" effect="dark" content="分配角色" placement="top">
              <el-button
                type="warning"
                icon="el-icon-s-tools"
                size="mini"
                @click="showAllotsRoleDialog(socpe.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页栏目 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 7, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- 添加用户对话框 -->
    <!-- 表单关闭时，不会重置数据，依然从缓存中加载输入过的数据，需要调用close方法进行重置 -->
    <el-dialog
      title="添加用户"
      :close-on-click-modal="false"
      @close="resetAddUserForm"
      :visible.sync="addFormVisible"
      width="30%"
    >
      <el-form :model="userForm" :rules="rules" ref="addUserForm">
        <!-- 想要显示规则提示，必须给form-item加上prop -->
        <el-form-item prop="username">
          <el-input v-model="userForm.username" placeholder="用户名" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="userForm.password" placeholder="密码" autocomplete="off" type="password"></el-input>
        </el-form-item>
        <el-form-item prop="email">
          <el-input v-model="userForm.email" placeholder="邮箱" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item prop="mobile">
          <el-input v-model="userForm.mobile" placeholder="电话号码" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="addFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 编辑用户对话框 -->
    <el-dialog
      title="编辑用户"
      @close="resetEditUserForm"
      :close-on-click-modal="false"
      :visible.sync="editFormVisible"
      width="30%"
    >
      <el-form :model="userForm" :rules="rules" ref="editFormRef">
        <el-form-item prop="username">
          <el-input v-model="userForm.username" placeholder="用户名" disabled></el-input>
        </el-form-item>
        <el-form-item prop="email">
          <el-input v-model="userForm.email" placeholder="邮箱"></el-input>
        </el-form-item>
        <el-form-item prop="mobile">
          <el-input v-model="userForm.mobile" placeholder="电话号码"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="editFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUser">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 分配角色对话框 -->
    <el-dialog
      title="分配角色"
      :close-on-click-modal="false"
      @close="resetSelectedInfo"
      :visible.sync="allotsRoleVisible"
      width="30%"
    >
      <p>当前用户： {{userInfo.username}}</p>
      <p>当前角色： {{userInfo.role_name}}</p>
      <p>
        分配新角色:
        <el-select placeholder="请选择角色" v-model="selectedId">
          <el-option
            v-for="item in roleList"
            :key="item.id"
            :label="item.roleName"
            :value="item.id"
          ></el-option>
        </el-select>
      </p>
      <div slot="footer">
        <el-button @click="allotsRoleVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateRole">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    // 自定义的手机号码校验规则，通过调用callback提示用户
    var checkMoblie = (rule, value, callback) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) return callback()
      callback(new Error('请输入11位合法的手机号码'))
    }
    return {
      // 显示加载进度
      loading: false,
      // 用户列表信息
      mUserList: [],
      // 页码总数
      total: 0,
      // 页码查询信息
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      // 是否显示添加用户对话框
      addFormVisible: false,
      // 是否显示编辑用户对话框
      editFormVisible: false,
      // 是否先是分配角色对话框
      allotsRoleVisible: false,
      // 添加用户表单序列化
      userForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 编辑表单序列化
      editUserID: 0,
      // 邮箱的校验规则
      rules: {
        // 用户名的校验规则
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '长度在 6 到 16 个字符', trigger: 'blur' }
        ],
        email: [
          {
            required: true,
            type: 'email',
            message: '请输入合法的邮箱',
            trigger: 'blur'
          }
        ],
        mobile: [
          { required: true, message: '请输入手机号码', trigger: 'blur' },
          { validator: checkMoblie, trigger: 'blur' }
        ]
      },
      // 分配角色对话框的用户信息
      userInfo: {
        userId: '',
        role_name: '',
        username: ''
      },
      // 角色列表
      roleList: [],
      // 分配角色选中的ID号 必须是空的字符串，才会显示placeholder里面的值
      selectedId: ''
    }
  },
  methods: {
    async getUsersList() {
      this.loading = true

      const { data, meta } = (await this.$http.get('users', {
        params: this.queryInfo
      })).data
      this.loading = false

      if (meta.status === 200) {
        this.mUserList = data.users
        this.total = data.total
      } else {
        this.$message.error(meta.msg)
      }
    },
    // 设置请求参数的页码显示条目
    handleSizeChange(val) {
      this.queryInfo.pagesize = val
      this.getUsersList()
    },
    // 设置当前页 上一页，下一页按钮，前往页码输入框调用
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val
      this.getUsersList()
    },
    // 改变用户列表中用户的状态
    async changeUserState(userInfo) {
      const { meta } = (await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      )).data

      if (meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('修改用户信息失败' + meta.msg)
      }
      this.$message.success('修改用户状态成功')
    },
    // 关闭对话框，重置表单数据
    resetAddUserForm() {
      this.$refs.addUserForm.resetFields()
    },
    // 关闭对话框，重置表单数据
    resetEditUserForm() {
      this.$refs.editFormRef.resetFields()
    },
    // 添加用户
    addUser() {
      this.$refs['addUserForm'].validate(async valid => {
        if (valid) {
          const { meta } = (await this.$http.post('users', this.userForm)).data
          if (meta.status === 201) {
            this.$message.success(meta.msg)
            this.getUsersList()
          } else {
            this.$message.error(meta.msg)
          }
        } else {
          this.$message.error('提交表单有误，检查填写是否正确')
        }
        // 关闭对话框必须写在异步响应接受到之后，否则会在异步请求期间调用，得到的表单值是空的
        this.addFormVisible = false
      })
    },
    // 显示编辑用户对话框
    async showEditDialog(id) {
      this.editFormVisible = true
      const { meta, data } = (await this.$http.get('users/' + id)).data
      if (meta.status === 200) {
        // 对话框上显示邮箱和电话号码
        this.editUserID = id
        this.userForm.username = data.username
        this.userForm.email = data.email
        this.userForm.mobile = data.mobile
      }
    },
    // 修改用户
    editUser() {
      this.$refs.editFormRef.validate(async valid => {
        // valid是表单预验证的结果，false 提示用户
        if (!valid) return this.$message.error('请检查表单数据是否合法')
        // 预验证通过
        const { meta } = (await this.$http.put('users/' + this.editUserID, {
          id: this.editUserID,
          email: this.userForm.username,
          mobile: this.userForm.mobile
        })).data
        if (meta.status === 200) {
          this.getUsersList()
          this.$message.success('修改用户成功')
        } else this.$message.error(meta.msg)
        // 关闭对话框
        this.editFormVisible = false
      })
    },
    // 删除用户 弹出消息框确认是否删除
    deleteUser(id) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { meta } = (await this.$http.delete('users/' + id)).data
          if (meta.status === 200) {
            this.$message.success('删除成功')
            this.getUsersList()
          } else this.$message.error(meta.msg)
        })
        .catch(() => {
          this.$message.info('取消删除')
        })
    },
    // 为用户分配角色 显示分配角色对话框
    async showAllotsRoleDialog(userinfo) {
      this.userInfo = userinfo

      const { meta, data } = (await this.$http.get('roles')).data
      if (meta.status === 200) {
        this.roleList = data
      } else this.$message.error(meta.msg)
      this.allotsRoleVisible = true
    },
    // 为用户分配新的角色
    async updateRole() {
      const { meta } = (await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.selectedId
      })).data
      if (meta.status === 200) {
        this.$message.success('更新用户角色成功')
        this.getUsersList()
      } else this.$message.error('更新用户角色失败！')
      this.allotsRoleVisible = false
    },
    // 关闭对话框，重置被选中的分配角色
    resetSelectedInfo() {
      this.selectedId = ''
      this.userInfo = {}
    }
  },
  mounted() {
    this.getUsersList()
  }
}
</script>

<style lang="less" scoped>
</style>
