<template>
  <div>
    <!-- 表头 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            class="input-with-select"
            :model="proName"
            clearable
            v-on:keyup.enter.native="searchPro"
          >
            <el-button slot="append" icon="el-icon-search" @click="searchPro"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddProduct">添加商品</el-button>
        </el-col>
      </el-row>
      <!-- 商品列表表格 -->
      <el-table
        ref="proTable"
        :data="mProductList"
        style="width: 100%"
        border
        stripe
        v-loading="loading"
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label="商品名称"></el-table-column>
        <el-table-column prop="goods_price" label="商品价格（元）" width="95px"></el-table-column>
        <el-table-column prop="goods_weight" label="商品重量" width="80px"></el-table-column>
        <el-table-column prop="add_time" label="创建时间" width="170px">
          <template slot-scope="scope">
            <div>{{scope.row.add_time|dateFormat}}</div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="130px">
          <template slot-scope="scope">
            <!-- 编辑按钮 -->
            <el-button size="mini" type="primary" class="el-icon-edit"></el-button>
            <!-- 删除按钮 -->
            <el-button
              size="mini"
              type="danger"
              class="el-icon-delete"
              @click="deleteProductById(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页栏目 加上background 属性，可以获得 页面背景的颜色-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 25, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="mTotal"
        background
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      // 搜索商品的名字
      proName: '',
      // 商品列表的数据
      mProductList: [],
      // 商品列表的总数
      mTotal: 0,
      // 查询商品列表的参数
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      }
    }
  },
  methods: {
    // 添加商品
    addProduct() {},
    // 搜索商品
    searchPro() {},
    // 获取商品列表
    async fetchProductList() {
      this.loading = true
      const { meta, data } = (await this.$http.get('goods', {
        params: this.queryInfo
      })).data
      this.loading = false
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mProductList = data.goods
      this.mTotal = data.total
      console.log(data)
    },
    // 分页栏目处理当前页的改变
    handleCurrentChange(curNum) {
      this.queryInfo.pagenum = curNum
      this.fetchProductList()
    },
    // 分页栏目处理页面显示最大条目的改变
    handleSizeChange(size) {
      this.queryInfo.pagesize = size
      this.fetchProductList()
    },
    // 是否删除某件商品 ，弹窗确认
    deleteProductById(id) {
      this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { meta } = (await this.$http.delete('goods/' + id)).data
          if (meta.status === 200) {
            this.$message.success('删除成功')
            this.fetchProductList()
          } else this.$message.error(meta.msg)
        })
        .catch(() => {
          this.$message.info('取消删除')
        })
    },
    // 添加商品
    goAddProduct() {
      this.$router.push('/goods/add')
    }
  },
  created() {
    this.fetchProductList()
  }
}
</script>
