<template>
  <div>
    <!-- 表头 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-alert title="添加商品信息" type="info" center show-icon :closable="false"></el-alert>
      <!-- 完成添加的步骤条  stepIndex 本身是字符串，但：active接受数字number类型，只需减去0,就可以转为字符串，ascii码相减差值是它本身代表的数字语义-->
      <el-steps :space="200" :active="stepIndex-0" finish-status="success" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- 由于每个步骤都是整体表单的一部分 所以表单应该包裹标签页 -->
      <!-- label-position表单域标签的位置，如果值为 left 或者 right 时，则需要设置 label-width -->
      <el-form :model="form" :rules="rules" ref="formRef" label-width="100px" label-position="top">
        <!-- 左侧标签栏 -->
        <!-- v-model绑定值，选中选项卡的 name string类型 -->
        <!-- :before-leave的方法不能自定义，只能使用驼峰命名的方法 -->
        <el-tabs
          :tab-position="'left'"
          v-model="stepIndex"
          :before-leave="beforeLeave"
          @tab-click="tabClick"
        >
          <!-- name不能加上： 否则就会作为数字0传递 此时name只接受字符串类型 -->
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="form.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="form.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="form.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="form.goods_number" type="number"></el-input>
            </el-form-item>
            <!-- 以','分割的分类列表 -->
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                v-model="form.goods_cat"
                :options="mCategoriesList"
                :props="cascaderProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
            <el-button @click="nextTab" class="goNext">下一页</el-button>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <!-- 渲染表单动态参数项 -->
            <el-form-item :label="item.attr_name" v-for="item in dynamicData" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="subItem" v-for="(subItem,i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
            <el-button @click="nextTab"  class="goNext">下一页</el-button>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <el-form-item :label="item.attr_name" v-for="item in staticData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>

            <el-button @click="nextTab" class="goNext">下一页</el-button>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <el-upload
              class="upload-demo"
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="picReqHeader"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
            <el-button @click="nextTab"  class="goNext">下一页</el-button>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编辑器 -->
            <quill-editor v-model="form.goods_introduce"></quill-editor>
            <el-button @click="addProduct" class="btn-addProduct" type="primary">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- 预览图片对话框 -->
    <el-dialog title="图片预览" :visible.sync="previewVisible" width="30%">
      <img :src="previewPath" alt width="100%" />
    </el-dialog>
  </div>
</template>

<script>
// 导入lodash数据结构处理工具

import _ from 'lodash'
export default {
  data() {
    return {
      // 完成到哪个步骤的角标
      stepIndex: '0',
      //   表单数据序列化
      form: {
        goods_name: '',
        goods_cat: [],
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        // 富文本编辑器的内容
        goods_introduce: '',
        // 图片列表
        pics: [],
        attrs: []
      },
      // 表单验证规则
      rules: {
        // 商品名称的校验规则
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
          { min: 2, max: 18, message: '长度在 2 到 18 个字符', trigger: 'blur' }
        ],
        // 商品价格校验规则
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        // 商品重量娇艳规则
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请输入商品分类', trigger: 'blur' }
        ]
      },
      //   商品分类列表
      mCategoriesList: [],
      //   配置级联选择器属性
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      //   选中的属性
      selectedOption: [],
      // 动态参数列表
      dynamicData: [],
      // 静态参数
      staticData: [],
      // 上传的图片内容
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 配置上传图片的请求头
      picReqHeader: {
        Authorization: window.sessionStorage.getItem('access_token')
      },
      // 预览图片地址
      previewPath: '',
      // 是否显示预览图片对话框
      previewVisible: false
    }
  },
  methods: {
    async fetchCateList() {
      const { meta, data } = (await this.$http.get('categories')).data
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mCategoriesList = data
      // console.log(this.mCategoriesList)
    },
    // 处理级联选择器被选中后
    handleChange() {
      if (this.form.goods_cat.length !== 3) {
        this.form.goods_cat = []
        this.$message.error('请选择三级分类')
      }
    },
    // 在标签页切换时调用
    beforeLeave(activeName, oldActiveName) {
      // console.log('即将进入的标签页' + activeName)
      // console.log('即将离开的标签页' + oldActiveName)
      if (oldActiveName === '0' && this.form.goods_cat.length !== 3) {
        this.$message.error('请选择商品的三级分类')
        return false
      }
    },
    // 当标签页被点击时
    async tabClick() {
      // console.log(typeof this.stepIndex) //string
      switch (this.stepIndex) {
        case '1':
          const { meta, data } = (await this.$http.get(
            `categories/${this.cateId}/attributes`,
            { params: { sel: 'many' } }
          )).data
          if (meta.status !== 200) return this.$message.error(meta.msg)
          this.dynamicData = data
          this.dynamicData.forEach(item => {
            item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          })
          // console.log(this.dynamicData)
          break
        case '2':
          const { meta: sMeta, data: sData } = (await this.$http.get(
            `categories/${this.cateId}/attributes`,
            { params: { sel: 'only' } }
          )).data
          if (sMeta.status !== 200) return this.$message.error(sMeta.msg)
          this.staticData = sData
          break
        default:
          break
      }
    },
    // 点击下一页
    nextTab() {
      // stepIndex本身是string类型，转为数值类型，进行+1,替换，然后再次转为字符串
      var stepNum = this.stepIndex - 0 + 1
      // 如果超过了栏目，重新回到第一个位置
      if (stepNum > 4) stepNum = 0
      this.stepIndex = stepNum + ''
      this.tabClick()
      // console.log(typeof this.stepIndex)
      // console.log(this.stepIndex)
    },
    // 展示上传的商品图片预览图
    handlePreview(file) {
      console.log(file)
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 移除上传的商品图片
    handleRemove(file, fileList) {
      const index = this.form.pics.findIndex(
        item => item.pic === file.response.data.tmp_path
      )
      this.form.pics.splice(index, 1)
    },
    handleSuccess(response, file, fileList) {
      if (response.meta.status !== 200) return this.$message.error('上传失败')
      this.form.pics.push({
        pic: response.data.tmp_path
      })
      this.$message.success('上传成功')
    },
    // 发送请求，添加商品
    addProduct() {
      this.$refs.formRef.validate(async valid => {
        if (!valid) return this.$message.error('添加必要的表单项')
        // 将cat_id转为字符串
        // 对表格进行深拷贝 即引用也会拷贝是两个独立的个体
        const copyForm = _.cloneDeep(this.form)
        copyForm.goods_cat = copyForm.goods_cat.join(',')

        // 处理attr动态参数和静态属性
        this.dynamicData.forEach(item => {
          let attrInfo = {}
          attrInfo.attr_id = item.attr_id
          attrInfo.attr_value = item.attr_vals
          copyForm.attrs.push(attrInfo)
        })

        this.staticData.forEach(item => {
          let attrInfo = {}
          attrInfo.attr_id = item.attr_id
          attrInfo.attr_value = item.attr_vals
          copyForm.attrs.push(attrInfo)
        })

        // 发送请求
        const { meta } = (await this.$http.post('goods', copyForm)).data
        console.log(meta)
        if (meta.status !== 201) return this.$message.error(meta.status)
        this.$message.success('添加商品成功')
        this.$router.push('/goods')
        // console.log(this.form)
        // console.log(copyForm)
      })
    }
  },
  created() {
    this.fetchCateList()
  },
  computed: {
    cateId() {
      if (this.form.goods_cat.length === 3) return this.form.goods_cat[2]
      return null
    }
  }
}
</script>

<style scoped>
.el-checkbox {
  margin-right: 0px !important;
}
.btn-addProduct {
  float: right;
  margin-top: 15px;
}
.goNext{
  margin-top:15px;
}
</style>
